:root{
    --box-height : 550px;
    --box-width : 1000px;
    --box-left-margin : calc(-1 * var(--box-width)/2);
    --box-top-margin : calc(-1 * var(--box-height)/2);
    --body-bg-color : #222831;
}

body{
    background-color: #222831;
    background-color: var(--body-bg-color);
}

.game-box{
    width: 1000px;
    width: var(--box-width);
    height: 550px;
    height: var(--box-height);
    margin : 50 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: calc(-1 * 550px/2);
    margin-top: var(--box-top-margin);  
    margin-left: calc(-1 * 1000px/2);  
    margin-left: var(--box-left-margin);
}



:root{
    --time-text-color : #eee;
    --time-text-font : "Roboto";
    --time-text-font-size : 16px;
    --title-color : #eee;
    --title-font : "Roboto";
    --status-bar-height : 30px;
    --status-bar-width : 100%;
    --status-bar-bg-color : #393E46;
}

.status-bar{
    box-sizing: border-box;
    width : 100%;
    width : var(--status-bar-width);
    height: 30px;
    height: var(--status-bar-height);
    background-color: #393E46;
    background-color: var(--status-bar-bg-color);
    line-height: 30px;
    line-height: var(--status-bar-height);
    padding : 0 10px;
    display: flex;
    -webkit-user-select: none;
            user-select: none;
}

.play-pause-button{
    display : inline-block;
    margin : 0 10px;
}

.play-pause-button:hover{
    cursor: pointer;
}

.time-text{
    padding : 0;
    margin : 0;
    display : inline-block;
    color : #eee;
    color : var(--time-text-color);
    font-family: "Roboto";
    font-family: var(--time-text-font);
    font-size: 16px;
    font-size: var(--time-text-font-size);
}

.restart-button{
    display : inline-block;
    margin : 0 10px;
    background-color : transparent;
    font-size: 20px;
}

.restart-button:hover{
    cursor: pointer;
    box-shadow: 0 0 2px white;
}

.title-container{
    box-sizing: border-box;
    display: inline-block;
    margin : 0 10px;
    flex-grow: 1;
    text-align: center;
}

.title{
    display: inline-block;
    width : 200px;
    margin : 0;
    color : #eee;
    color : var(--title-color);
    font-family: "Roboto";
    font-family: var(--title-font);
    -webkit-user-select: none;
            user-select: none;
}

.dropdown-content{
    z-index: 1;
    display: inline-block;
    margin-top : calc(-1 * 30px);
    margin-top : calc(-1 * var(--status-bar-height));
    display: none;
    position : relative;
    background-color : #393E46;
    background-color : var(--status-bar-bg-color);
    border-radius: 5px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.title:hover .dropdown-content{
    display:block;
}

.dropdown-item{
    list-style: none;
    color : white;
}

.dropdown-item:hover{
    background-color : #4a4a4a;
}

.speed-buttons{
    margin : 0 5px;
    display : inline-block;
    box-sizing: border-box;
    line-height: 30px;
    line-height: var(--status-bar-height);
    height : 20px;
    text-align: center;
}


.reduce-speed, .increase-speed{
    display : inline-block;
    display : invisible;
    padding : 0 5px;
    color : transparent;
}

.reduce-speed:hover, .increase-speed:hover{
    display : visible;
    cursor: pointer;    
    color : powderblue;
    box-shadow: 0 0 1px white;
}

.speed-text{
    box-sizing: border-box;
    width : 40px;
    display : inline-block;
    font-family: "Roboto";
    font-family: var(--time-text-font);
    font-size: 16px;
    font-size: var(--time-text-font-size);
    color : powderblue;
}

.speed-text:hover{
    box-shadow: 0 0 1px white;
}

.custom-input{
    width : 80px;
    height : 30px;
    height : var(--status-bar-height);
    background-color: transparent;
    border-radius: 5px;
    margin-right: 30px;
    text-align: center;
    box-shadow : 0 0 0 transparent;
    border : 0 solid transparent;
    border-radius: 5px;
    color : #eee;
    color : var(--time-text-color);
    font-size: 15px;
    font-family: "Roboto";
    font-family: var(--time-text-font);
}

.custom-input:hover{
    box-shadow: 0 0 2px white;
    cursor : pointer;
}
:root{
    --plain-text-color : #333;
    --canvas-width: var(--box-width);
    --canvas-height : calc(var(--box-height) - var(--status-bar-height));
    --canvas-bg-color : #23120B;
    --outer-container-height : 500px;
    --outer-container-width : 900px;
    --index-text-font : "Arial";
}

.canvas{
    box-sizing: border-box;
    background-color: #23120B;
    background-color: var(--canvas-bg-color);
    width: var(--box-width);
    width: var(--canvas-width);
    height : calc(var(--box-height) - var(--status-bar-height));
    height : var(--canvas-height);
    line-height: calc(var(--box-height) - var(--status-bar-height));
    line-height: var(--canvas-height);
}

.plain-text{
    color : #333;
    color : var(--plain-text-color);
    padding : 0;
    margin : 0;
}

.outer-container{
    height : 500px;
    height : var(--outer-container-height);
    width : 900px;
    width : var(--outer-container-width);
    position: absolute;
    top : 50%;
    left : 50%;
    margin-top: calc(-1 * 500px/2);
    margin-top: calc(-1 * var(--outer-container-height)/2);
    margin-left : calc(-1 * 900px/2);
    margin-left : calc(-1 * var(--outer-container-width)/2);
    display : flex;
    flex-direction: column;
}

.bars-container{
    box-sizing: border-box;
    height : 85%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content : flex-end;
}

.bar{
    box-sizing: border-box;
    margin : 3px;
    flex-grow : 1;
    align-self: flex-end;
    box-sizing: border-box;
}

.line {
    width: 100%;
    height : 2px;
    background-color: white;
    border-radius : 10px;
}

.indices-container{
    box-sizing: border-box;
    width: 100%;
    /* background-color: blue; */
    flex-grow: 1;
    display: flex;
    flex-direction: row;
}

.index{
    -webkit-user-select: none;
            user-select: none;
    box-sizing: border-box;
    border : 2px solid;
    border-radius: 5px;
    margin : 3px;
    flex-grow : 1;
    width : 1px;
    font-size : 16px;
    line-height: 35px;
    font-family : "Arial";
    font-family : var(--index-text-font);
    text-align: center;
}
:root{
    --textbox-container-height : 50px;
    --textbox-container-width : 100%;
    --textbox-bg-color : #393E46;
}

.textbox-container{
    box-sizing: border-box;
    color : white;
    font-size: 20px;
    line-height: 1.2;
    max-width: 100%;
    max-width: var(--textbox-container-width);
    min-width: 100%;
    min-width: var(--textbox-container-width);
    display : flex;
    flex-direction: row;
}

.textbox-button{
    font-size: 20px;
    height : 50px;
    height : var(--textbox-container-height);
    width : 80px;
    background-color: #333;
    text-align: center;
    color : white;
    box-shadow : 0 0 0 transparent;
    border : 0 solid transparent;
    border-radius: 5px;
}

.textbox-button:hover{
    box-shadow : 0 0 5px white;
    cursor : pointer;
}

.textbox{
    box-sizing: border-box;
    padding : 0 10px;
    color : white;
    font-size: 20px;
    line-height: 1.2;
    min-height: 50px;
    min-height: var(--textbox-container-height);
    max-height: 50px;
    max-height: var(--textbox-container-height);
    background-color: #393E46;
    background-color: var(--textbox-bg-color);
    flex-grow : 1;    
    border : none;
}

.textbox:active{
    outline : none;
}

.textbox:focus{
    outline : none;
    box-shadow : 0 0 4px yellow;
}

